module.exports = "* {\n  font-family: 'Cormorant Upright', serif;\n}\n\n.loading-message {\n  font-weight: bold;\n  font-size: 32px;\n  letter-spacing: 0.05em;\n  color: #C4C4C4;\n}\n\n.loader {\n  font-size: 10px;\n  margin: 50px auto;\n  text-indent: -9999em;\n  width: 11em;\n  height: 11em;\n  border-radius: 50%;\n  background: #ffffff;\n  background: linear-gradient(to right, #ffffff 10%, rgba(255, 255, 255, 0) 42%);\n  position: relative;\n  -webkit-animation: load3 1.4s infinite linear;\n  animation: load3 1.4s infinite linear;\n  -webkit-transform: translateZ(0);\n  -ms-transform: translateZ(0);\n  transform: translateZ(0);\n}\n\n.loader:before {\n  width: 50%;\n  height: 50%;\n  background: #ffffff;\n  border-radius: 100% 0 0 0;\n  position: absolute;\n  top: 0;\n  left: 0;\n  content: '';\n}\n\n.loader:after {\n  background: #000000;\n  width: 75%;\n  height: 75%;\n  border-radius: 50%;\n  content: '';\n  margin: auto;\n  position: absolute;\n  top: 0;\n  left: 0;\n  bottom: 0;\n  right: 0;\n}\n\n@-webkit-keyframes load3 {\n  0% {\n    -webkit-transform: rotate(0deg);\n    transform: rotate(0deg);\n  }\n\n  100% {\n    -webkit-transform: rotate(360deg);\n    transform: rotate(360deg);\n  }\n}\n\n@keyframes load3 {\n  0% {\n    -webkit-transform: rotate(0deg);\n    transform: rotate(0deg);\n  }\n\n  100% {\n    -webkit-transform: rotate(360deg);\n    transform: rotate(360deg);\n  }\n}\n\n.book-page-left,\n.book-page-right {\n  overflow: scroll;\n  height: 550px;\n  background: #EEECE1;\n\n}\n\n.book-page-left {\n  border-radius: 25px 50px 0px 25px;\n  border: 0.5px solid #000000;\n}\n\n.book-page-right {\n  border: 0.5px solid #000000;\n  border-radius: 50px 25px 25px 0px;\n}\n\n.toolbar {\n  background: #F1EDED;\n  box-shadow: 0px 0px 10px rgba(0, 0, 0, 0.24), 0px 2px 10px rgba(0, 0, 0, 0.24);\n  border-radius: 0px 0px 25px 25px;\n  height: 40px;\n}\n\n.toolbar img {\n  cursor: pointer;\n}\n\n.color-picker {\n  width: 208px;\n  height: 115px;\n  padding: 10px 20px;\n  margin-left: 60px;\n  margin-top: 5px;\n  background: #F3F3F3;\n  box-shadow: 0px 2px 5px rgba(0, 0, 0, 0.24), 0px 0px 5px rgba(0, 0, 0, 0.24);\n  border-radius: 0px 0px 5px 5px;\n}\n"